    letter-spacing: 0;
    line-height: 40px;
    font-family: Jumble;
    margin-right: 25px;
    cursor: pointer;
    text-transform: uppercase;
    opacity: .9;
    transition: all .1s ease-in-out
}

.nav-item:hover {
    filter: drop-shadow(0 0 1px #ffffff);
    opacity: 1;
    transform: translateY(-1px)
}

.logo-png-transparent {
    height: 80%;
    z-index: 100;
    margin-right: 30px;
    margin-top: -15px;
    cursor: pointer
}

.logo-png-transparent:hover {
    filter: drop-shadow(0 0 1px #ffffff);
    opacity: 1;
    transform: translateY(-1px)
}

.discord-button2 {
    font-family: Jumble;
    font-weight: 800;
    background-color: #e53261;
    border-radius: 20px;
    padding: 10px 22px;
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 50px;
    line-height: 50px;
    text-transform: uppercase;
    opacity: .9;
    transition: all .1s ease-in-out
}

.discord-button2 .fab {
    font-size: 35px
}

.discord-button2:hover {
    filter: drop-shadow(0 0 5px #e53261);
    opacity: 1;
    transform: translateY(-1px)
}

.join-us-on-discord2 {
    color: #fff;
    font-family: var(--font-family-jaapokki-regular);
    font-size: 21px;
    font-weight: 600;
    letter-spacing: 0;
    margin-left: 10px;
    margin-top: 5px;
    text-align: center
}

.discord-logo {
    width: 40px
}

.right {
    width: 180px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center
}

.right .fab {
    font-size: 45px;
    cursor: pointer;
    opacity: .9;
    transition: all .1s ease-in-out
}

.right .fab:hover {
    filter: drop-shadow(0 0 1px #ffffff);
    opacity: 1;
    transform: translateY(-1px)
}

.right .heelshoe {
    cursor: pointer;
    height: 45px;
    opacity: .9
}

.right .heelshoe:hover {
    filter: drop-shadow(0 0 1px #ffffff);
    opacity: 1;
    transform: translateY(-1px)
}

.burger {
    display: none;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    margin-right: -20px
}

.burger .fa {
    font-size: 50px;
    padding: 10px;
    padding-left: 15px;
    padding-right: 15px;
    cursor: pointer;
    opacity: .9;
    transition: all .1s ease-in-out
}

.burger .fa:hover {
    filter: drop-shadow(0 0 1px #ffffff);
    opacity: 1;
    transform: translateY(-1px)
}

.mobile {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    position: absolute;
    top: 80px;
    left: 0;
    width: 100%
}

.mob-nav-item {
    align-items: center;
    white-space: nowrap;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
