.mob-nav-item:hover {
    filter: drop-shadow(0 0 1px #ffffff);
    opacity: 1
}

.mobLogo {
    display: flex;
    flex-direction: row;
    justify-content: center
}

.mobLogo .fab {
    font-size: 35px;
    cursor: pointer;
    opacity: .9;
    transition: all .1s ease-in-out;
    margin-right: 10px;
    margin-left: 10px
}

